<template>


<div class="home">
    <!-- header -->
    <mt-header fixed title="è‘¡è„ToDo">
        <mt-button  slot="left"  @click.native="popupVisible = true">
          <img src="../assets/menu.png">
        </mt-button>
        <mt-button class="mr10" slot="right" @click.native="addPrompt">
          <img src="../assets/add.png">
        </mt-button>
        <mt-button  slot="right">
          <img src="../assets/more.png">
        </mt-button>
    </mt-header>

    <!-- å†…å®¹å®¹å™¨ -->
    <ul class="todo-list">
      <li :class="todo.completed ? 'classA' : 'classB' " v-for="(todo,index) in todos1" >
        <div class="view">
          <label @click="getIndex(index)" >
            {{ todo.title }}
          </label>
        </div>          
      </li>
    </ul>

     <!-- ä¸€ç³»åˆ—å¼¹å‡ºæ¡† -->

    <!-- action-sheet ç‚¹å‡»TODOäº‹ä»¶åŽå±å¹•ä¸‹æ–¹å¼¹å‡ºé€‰é¡¹ -->
     <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>

    <!-- popup  ç‚¹å‡»å¼¹å‡ºå·¦ä¾§å¯¼èˆªèœå•-->
     <mt-popup v-model="popupVisible" position="left" class="popup-3" popup-transition="popup-fade">
        <Menu></Menu>   
    </mt-popup>

  </div> 

</template>

<script>

// mintui å¼•ç”¨
import Vue from 'vue'
import { Popup, MessageBox, Actionsheet } from 'mint-ui'
Vue.component(Popup.name, Popup)
Vue.component(MessageBox.name, MessageBox);
Vue.component(Actionsheet.name, Actionsheet);
import Menu from '@/components/Menu.vue'

// çŠ¶æ€
var filters = {
  	all: function (todos1) {
   		return todos;
  	},
  	active: function (todos1) {
   		return todos1.filter(function (todo) {
     		return !todo.completed;
   		});
  	},
  	completed: function (todos1) {
   		return todos.filter(function (todo) {
     		return todo.completed;
   		});
  	}
  };


export default {
  name: 'home', 

  data(){
    return{ 
      popupVisible:false,     //å·¦ä¾§å¯¼èˆªèœå•ä¸å¯è§
      sheetVisible:false,     //action-sheetç»„ä»¶ä¸å¯è§
      actions:[],             //å­˜æ”¾action-sheetç»„ä»¶é‡Œçš„é€‰é¡¹æ•°æ®nameå’Œmethods
      todos1:[],              //æ•°ç»„ï¼Œå­˜æ”¾æ¯æ¬¡æ·»åŠ TODOäº‹ä»¶pushè¿›æ¥çš„æ•°æ®
      index:'',               //å­˜æ”¾ç‚¹å‡»todos1æ•°ç»„å¯¹è±¡æ‰€èŽ·å¾—ç›¸å¯¹åº”çš„ä¸‹æ ‡index
      value:'',               //å­˜æ”¾indexç›¸å¯¹åº”çš„todos1.titleæ•°æ®
      activeColor :'red',
    } 
   },
   components:{
    Menu          //å·¦ä¾§å¯¼èˆªèœå•
   },
   // watch:{
   //    todos: {
   //           handler: function (todos1) {
   //             todoStorage.save(todos1);
   //           },
   //           deep: true
   //       }
   // },
   mounted() {
    // é’©å­å‡½æ•°ï¼Œaction-sheetç»„ä»¶é‡Œçš„é€‰é¡¹æ•°æ®nameå’Œmethods
      this.actions = [{
        name: 'å·²å®Œæˆ',
        method: this.doneTodo
      }, {
        name: 'ç¼–è¾‘',
        method: this.editTodo     
      }, {
        name: 'åˆ é™¤',
        method: this.removeTodo
      }];
    },

   methods:{
    // å‡½æ•°æ–¹æ³•

    // å¼¹å‡ºä¿¡æ¯æ¡†
   	addPrompt(){
   		var that= this;
   		MessageBox.prompt(' ','æ·»åŠ ToDo').then(({ value }) => {
   			if (value) {
   				that.todos1.push({ title: value, completed: false });
   				console.log(that.todos1);
   				MessageBox.alert(`æ·»åŠ çš„TODOæ˜¯${value}`,'æ·»åŠ æˆåŠŸ')
   			};
   		})
   	},

    // ä½¿action-sheetç»„ä»¶å¯è§ï¼Œå¹¶ä¸”èŽ·å–ç›¸å¯¹åº”ä¸‹æ ‡indexå’Œå†…å®¹titleï¼Œåˆ†åˆ«æ”¾è¿›å…¨å±€å‚æ•°indexå’Œvalue
    getIndex(index){
      this.sheetVisible = true;
      this.index = index;
      this.value = this.todos1[this.index].title;
    },

    //ç‚¹å‡»å·²å®Œæˆé€‰é¡¹ï¼ŒçŠ¶æ€completedæ”¹å˜ =ã€‹ false -ã€‹true ;ä»Žè€Œliçš„classæ ·å¼ç”± classB è½¬å˜ classA
    doneTodo(){
      this.todos1[this.index].completed = true ;
    },

    // ç‚¹å‡»ç¼–è¾‘é€‰é¡¹ï¼Œå¼¹å‡ºä¿¡æ¯æ¡†ï¼Œè¾“å…¥å¹¶ç¼–è¾‘
    editTodo(){
      MessageBox.prompt(' ', 'ç¼–è¾‘ToDo', {inputPlaceholder: this.value} ).then(({ value, action }) => {
        this.todos1[this.index].title = value;
      });
      this.todos1[this.index].completed = false ;
    },

    // ç‚¹å‡»åˆ é™¤é€‰é¡¹ï¼Œä»Žæ•°ç»„todos1ä¸­åˆ é™¤å¯¹åº”çš„æ•°æ®
   	removeTodo(index){
   		this.todos1.splice(this.index,1);
   	}   	
   },

   computed: {
    filteredTodos: function () {
        return filters[this.visibility](this.todos1);
    }
   }
   
}

</script>
<style>
*{
	margin: 0;
	padding:0;
}
.mr10{
  margin-right:10px;
}
.mint-header {
	background-color: #66CCCC !important;
}
ul{
	list-style:none; 
}
.classA{
  background: #dbdbdb;
}
.classB{
  background: #ccffcc
}
.todo-list li{
	text-align: center;
	margin: 10px 0;
	height: 45px;
	width: 90%;
	display: inline-block;
	line-height: 45px
}

label {
    width: 100%
}
.popup-3 {
        width: 80%;
        height: 100%;
        background-color: #fff;
}
.mint-msgbox-header {
    padding: 15px 0;
    background: #66CCCC !important;
}
.mint-msgbox-title {
    color: #fff !important;
}
</style>


